___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 1024px) {
  .survey-description .content-one {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .form-section form {
    width: 100%;
    max-width: 400px;
    min-width: 300px;
  }

  #p1 {
    font-size: 18px;
    padding: 20px;
  }

  .surveys-title p {
    font-size: 18px;
  }

  .benefits-container {
    grid-template-columns: 1fr;
  }

  .progress-container {
    width: 70%;
    /* Mengurangi lebar pada perangkat dengan lebar layar lebih kecil */
  }
}

@media screen and (max-width: 480px) {
  .form-section form {
    width: 100%;
    max-width: 270px;
    min-width: 200px;
  }

  .card-benefit {
    margin: 5px 0;
  }

  .benefit-conclusion {
    padding: 0 10px;
  }

  .progress-container {
    width: 90%;
    /* Mengurangi lebar lebih lanjut pada perangkat dengan lebar layar sangat kecil */
  }

  .low {
    font-size: small;
  }

  .medium {
    font-size: small;
  }

  .high {
    font-size: small;
  }

  /* Footer responsive styles */
  .footer-top {
    flex-direction: column;
    align-items: center;
  }

  .footer-left,
  .footer-center,
  .footer-right {
    min-width: 100%;
    text-align: center;
  }

  .footer-links {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .footer-links li {
    margin: 10px 0;
  }

  .footer-icons {
    margin-top: 10px;
  }
}

@media screen and (max-width: 320px) {
  .logo {
    font-size: 10pt;
  }

  .tombol {
    font-size: 12pt;
  }

  .form-section form {
    width: 100%;
    max-width: 220px;
    min-width: 200px;
  }

  .benefits-title h1 {
    font-size: 1.5em;
  }

  .benefit-conclusion h3 {
    font-size: 1.2em;
  }

  .benefit-conclusion p {
    font-size: 0.9em;
  }

  /* Footer responsive styles for small screens */
  .footer-links li {
    margin: 5px 0;
  }

  .footer-icons i {
    font-size: 20px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/survey-responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,WAAW;EACb;AACF;;AAEA;EACE;IACE,WAAW;IACX,gBAAgB;IAChB,gBAAgB;EAClB;;EAEA;IACE,eAAe;IACf,aAAa;EACf;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,0BAA0B;EAC5B;;EAEA;IACE,UAAU;IACV,mEAAmE;EACrE;AACF;;AAEA;EACE;IACE,WAAW;IACX,gBAAgB;IAChB,gBAAgB;EAClB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,UAAU;IACV,iFAAiF;EACnF;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,gBAAgB;EAClB;;EAEA,6BAA6B;EAC7B;IACE,sBAAsB;IACtB,mBAAmB;EACrB;;EAEA;;;IAGE,eAAe;IACf,kBAAkB;EACpB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,mBAAmB;EACrB;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,gBAAgB;EAClB;AACF;;AAEA;EACE;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,WAAW;IACX,gBAAgB;IAChB,gBAAgB;EAClB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,gBAAgB;EAClB;;EAEA,+CAA+C;EAC/C;IACE,aAAa;EACf;;EAEA;IACE,eAAe;EACjB;AACF","sourcesContent":["@media screen and (max-width: 1024px) {\r\n  .survey-description .content-one {\r\n    width: 100%;\r\n  }\r\n}\r\n\r\n@media screen and (max-width: 768px) {\r\n  .form-section form {\r\n    width: 100%;\r\n    max-width: 400px;\r\n    min-width: 300px;\r\n  }\r\n\r\n  #p1 {\r\n    font-size: 18px;\r\n    padding: 20px;\r\n  }\r\n\r\n  .surveys-title p {\r\n    font-size: 18px;\r\n  }\r\n\r\n  .benefits-container {\r\n    grid-template-columns: 1fr;\r\n  }\r\n\r\n  .progress-container {\r\n    width: 70%;\r\n    /* Mengurangi lebar pada perangkat dengan lebar layar lebih kecil */\r\n  }\r\n}\r\n\r\n@media screen and (max-width: 480px) {\r\n  .form-section form {\r\n    width: 100%;\r\n    max-width: 270px;\r\n    min-width: 200px;\r\n  }\r\n\r\n  .card-benefit {\r\n    margin: 5px 0;\r\n  }\r\n\r\n  .benefit-conclusion {\r\n    padding: 0 10px;\r\n  }\r\n\r\n  .progress-container {\r\n    width: 90%;\r\n    /* Mengurangi lebar lebih lanjut pada perangkat dengan lebar layar sangat kecil */\r\n  }\r\n\r\n  .low {\r\n    font-size: small;\r\n  }\r\n\r\n